 <template>
    <div class="modal_container" v-bind:class="{'md-show':mdShow}">
	    <section class="tip_text_container">
            <div class="tip_icon">
                <img v-if="alertType=='success'" src="./../images/modal_success.png" alt="">
            </div>
            <p class="tip_text">{{alertText}}</p>
        </section>
    </div>
</template>

<script>
    export default {
    	data(){
            return{
               
            }
        },
        mounted(){

        },
        created(){
        },
        computed:{

        },
        props: ['alertText','alertType','mdShow'],
        methods: {
        }
    }
</script>

<style lang="scss" scoped>
    @import './../style/mixin';
    .modal_container{
    	position: fixed;
    	top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
        background: rgba(0,0,0,0.05);
        visibility: hidden;
    }
    .md-show{
        visibility: visible;
    }
    .tip_text_container{
        @include center;
        background: #fff;
        border-radius: 10px;
        text-align: center;
        box-shadow: 0px 2px 10px 0px #dfeff8;
        padding: 30px 40px;
        transition:all 0.5s;
        margin-top:-10px;
        opacity: 0;
        .tip_text{
            font-size: 14px;
            color: #35383A;
        }
    }
    .md-show .tip_text_container{
        //animation:show 0.5s;
        margin-top: 0px;
        opacity: 1;
    }
    
</style>



