<template>
  <div class="personal-content-body">
    <div class="content">
      <ul class="personal-nav-items">
        <li class="personal-nav-item cursor" @click="switchNav('info',$event)" :class="{personalActive:personalNav=='info'}">
          个人信息
        </li>
        <li class="personal-nav-item cursor" @click="switchNav('password',$event)" :class="{personalActive:personalNav=='password'}">
          密码设置
        </li>
      </ul>
      <div v-if="personalNav === 'info'" class="personal-info">
        <div class="personal-info-line personal-info-icon">
          <div>头像</div>
          <div>
            <label for="upload-img-input" class="cursor">
              <img v-if="uploadImageUrl==''" src="../../images/personal_mor_img.png" class="avatar">
              <!-- <img :src="uploadImageUrl" class="avatar"> -->
              <img v-else :src="uploadImageUrl" class="avatar">
              <a class="picture-btn">修改头像</a>
            </label>
          </div>
          <input type="file" v-on:change="changeToop($event)" ref="inputer" id="upload-img-input" style="display:none" />
          <div @click="toEditor" class="editor-box" :class="{isEditorBtn:isEditor}">
            <!-- <img v-if='isEditor' class="icon-editor" src="../../images/personal-editor.png" alt=""> -->
            <span class="icon-editor"></span>
            <!-- <img class="icon-editor" src="../../images/personal-editor1.png" alt=""> -->
            <span class="editor-btn">编辑</span>
          </div>
        </div>
        <div class="personal-info-line">
          <div>姓名</div>
          <el-input v-if='isEditor' v-model="personalInfo.name"></el-input>
          <div v-else>{{personalInfo.name}}</div>
        </div>
        <div class="personal-info-line">
          <div>部门</div>
          <el-select v-if='isEditor' v-model="selectdName">
            <el-option v-for="item in personalInfo.department" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
          <div v-else>{{personalInfo.dname}}</div>
        </div>
        <div class="personal-info-line">
          <div>职务</div>
          <el-input v-if='isEditor' v-model="personalInfo.position"></el-input>
          <div v-else>{{personalInfo.position}}</div>
        </div>
        <div class="personal-info-line">
          <div>手机号</div>
          <el-input v-if='isEditor' v-model="personalInfo.phone" maxLength='11'></el-input>
          <div v-else>{{personalInfo.phone}}</div>
        </div>
        <div class="personal-info-line">
          <div>邮箱</div>
          <el-input v-if='isEditor' v-model="personalInfo.email"></el-input>
          <div v-else>{{personalInfo.email}}</div>
        </div>
        <div class="personal-info-line">
          <div>备注</div>
          <el-input v-if='isEditor' type="textarea" :rows="2" v-model="personalInfo.memo"></el-input>
          <div v-else>{{personalInfo.memo}}</div>
        </div>
        <div v-if="isEditor" class="personal-info-line personal-editor-sublim">
          <el-button @click="cancleEditor" class="personal-cancle-btn"><span>取消</span></el-button>
          <el-button @click="toSublime" type="primary" class="personal-success-btn"><span>确定</span></el-button>
        </div>
      </div>
      <div class="personal-password" v-if="personalNav === 'password'">
        <!-- <div v-if="!isEditorPassword">
                        <div class="personal-info-line">
                            <div>账户密码：</div>
                            <div>*******</div>
                            <div @click="toEditorPassword" :class="{cursor: !isEditorPassword}">
                                <img v-if='isEditorPassword' class="icon-editor" src="../../images/personal-editor1.png" alt="">
                                <img v-else class="icon-editor" src="../../images/personal-editor.png" alt="">
                                <span class="editor-btn" :class="{isEditorBtn:isEditorPassword}">编辑</span>
                            </div>
                        </div>
                    </div> -->
        <!-- <div> -->
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="130px" class="ruleForm">
          <el-form-item label="输入原密码:" prop="oldPass">
            <el-input v-model="ruleForm.oldPass" auto-complete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item label="输入新密码:" prop="pass">
            <el-input v-model="ruleForm.pass" auto-complete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item label="确认新密码:" prop="checkPass">
            <el-input v-model="ruleForm.checkPass" auto-complete="off" type="password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="toSublimePassword('ruleForm')" type="primary" class="personal-success-btn"><span>确认提交</span></el-button>
          </el-form-item>
        </el-form>
        <!-- </div> -->
      </div>
      <modal v-bind:alertText="'修改成功'" v-bind:alertType="'success'" :mdShow="showAlert">
      </modal>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Modal from '../../components/modal.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import {
  addSignature,
  setStore,
  getStore,
  removeStore
} from '../../config/mUtils'
import {
  getUserInfo,
  editorUserInfo,
  upLoadFile,
  alertPassword
} from '../../config/api'
import { mapState, mapMutations } from 'vuex'
import api from "../../config/url.js";
export default {
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('*请输入密码'));
      } else {
        if (!this.oldePassError) {
          if (this.ruleForm.pass !== '') {
            this.$refs.ruleForm.validateField('pass');
          }
          callback();
        } else {
          if (this.ruleForm.oldPass == this.errPassword) {
            callback(new Error('*原密码输入错误'));
          }
          callback();
        }

      }
    };
    let validatePass1 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('*请输入新密码'));
      } else {
        // console.log(value);
        if (value.length < 6) {
          callback(new Error('*密码不能少于6位'));
        } else {
          if (value == this.ruleForm.oldPass) {
            callback(new Error('*新密码不能与原密码相同'));
          } else {
            if (this.ruleForm.checkPass !== '') {
              this.$refs.ruleForm.validateField('checkPass');
            }
            callback();
          }
        }

      }
    };

    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('*请再次输入密码'));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('*密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      personalNav: 'info', //进入默认选择个人信息
      isEditor: false, //判断是否是信息编辑状态
      isEditorPassword: false, //判断是否是密码修改状态
      editorCopy: null, //存放个人信息修改前数据
      uploadImageUrl: '', //头像地址,
      showAlert: false,
      oldePassError: false,
      errPassword: null,
      selectdName: '',
      img: null,
      //个人信息表单数据
      personalInfo: {},
      //修改密码表单数据
      ruleForm: {
        oldPass: '',
        pass: '',
        checkPass: '',
      },
      //规定每行数据的验证方式的对象
      rules: {
        oldPass: [{
          validator: validatePass,
          trigger: 'blur'
        }],
        pass: [{
          validator: validatePass1,
          trigger: 'blur'
        }],
        checkPass: [{
          validator: validatePass2,
          trigger: 'blur'
        }]
      }
    }
  },
  components: {
    Modal
  },
  mounted() {
    getUserInfo(null).then(res => {
      // console.log(res);
      if (res.data.msgNo == 1) {
        // console.log(res.data.payload)
        this.personalInfo = res.data.payload;
        this.uploadImageUrl = res.data.payload.url;
        // this.selectdName = res.data.payload.dname
        for (let i of this.personalInfo.department) {
          if (i.name == this.personalInfo.dname) {
            this.selectdName = i.id;
          }
        }
      }
    });
  },
  methods: {
    ...mapMutations([
      'RECORD_USERINFO', 'RECORD_USERURL', 'RECORD_USERNAME'
    ]),
    //显示图片
    // imgPreview (file) {
    //     let self = this;
    //     // 看支持不支持FileReader
    //     if (!file || !window.FileReader) return;

    //     // 创建一个reader
    //     var reader = new FileReader();
    //     // 将图片将转成 base64 格式
    //     reader.readAsDataURL(file);
    //     // 读取成功后的回调
    //     reader.onloadend = function () {
    //         self.uploadImageUrl = this.result;
    //     }    
    // },
    changeToop(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      //console.log(this.$refs.inputer);
      if (file == undefined) {
        return false;
      }
      // console.log(file.type);
      if (!/.(gif|jpg|jpeg|png)$/.test(file.type)) {
        // this.$message.error('上传头像只能是图片格式!');
        dialogConfirm('info', '上传头像图片只能是jpg/png/gif格式')
        inputDOM.value = '';
        return false
      }
      // let size = Math.floor(file.size / 1024);
      // if (size > 3) {
      //     // 这里可以加个文件大小控制
      //     return false
      // }
      upLoadFile(file).then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          // this.$message({
          //     message: '头像修改成功',
          //     type: 'success'
          // })
          dialogConfirm('success', '头像修改成功')
          // console.log(res.data.payload)
          this.uploadImageUrl = res.data.payload;
          let userinfo = JSON.parse(getStore('htcx_userInfo'));
          userinfo.url = res.data.payload;
          setStore('htcx_userInfo', userinfo);
          setStore('htcx_userUrl', res.data.payload);
          this.RECORD_USERINFO(getStore("htcx_userInfo"));
          this.RECORD_USERURL(getStore("htcx_userUrl"));
        }
      })
    },

    //去编辑
    toEditor() {
      if (!this.isEditor) {
        this.isEditor = true;
        this.editorCopy = JSON.parse(JSON.stringify(this.personalInfo));
      }
    },
    //取消编辑
    cancleEditor() {
      // debugger;

      if (JSON.stringify(this.personalInfo) != JSON.stringify(this.editorCopy)) {
        dialogConfirmHasbtn('确定不提交并关闭吗？').then(() => {
          this.personalInfo = this.editorCopy
          this.isEditor = false;
        }).catch(() => {});
      } else {
        this.isEditor = false;
      }

    },
    //提交
    toSublime() {
      let _that = this;
      let params = addSignature({
        name: this.personalInfo.name,
        email: this.personalInfo.email,
        phone: this.personalInfo.phone,
        position: this.personalInfo.position,
        depart_id: this.selectdName,
        memo: this.personalInfo.memo,
      })
      // if(this.personalInfo.img != undefined){
      //     params.img = this.personalInfo.img;
      // }
      // console.log(params);
      editorUserInfo(params).then(res => {
        // console.log(res.data.payload);
        if (res.data.msgNo == 1) {
          // this.$message({
          //     message: "用户信息修改成功",
          //     type: 'success'
          // })
          let userinfo = JSON.parse(getStore('htcx_userInfo'));
          userinfo.username = this.personalInfo.name;
          setStore('htcx_userInfo', userinfo);
          _that.RECORD_USERINFO(getStore("htcx_userInfo"));
          setStore('htcx_userName', this.personalInfo.name);
          //console.log(getStore("htcx_userName"));
          _that.RECORD_USERNAME(getStore("htcx_userName"));
          dialogConfirm('success', '用户信息修改成功')
          for (let i of this.personalInfo.department) {
            if (i.id == this.selectdName) {
              this.personalInfo.dname = i.name;
            }
          }
          // this.personalInfo.dname = this.selectdName;
          this.isEditor = false;
        } else {

          if (res.data.message == '手机号码长度不对') {
            dialogConfirm('info', "手机号码格式不正确");
          } else {
            dialogConfirm('info', res.data.message);
          }
        }
      });


    },
    //导航切换
    switchNav(type) {
      switch (type) {
        case 'info':
          this.personalNav = 'info'
          break;
        case 'password':
          this.personalNav = 'password'
          break;
      }
    },
    //去编辑密码 
    // toEditorPassword() {
    //     if (!this.isEditorPassword) {
    //         this.isEditorPassword = true;
    //         this.ruleForm = {
    //             oldPass: '',
    //             pass: '',
    //             checkPass: '',
    //         };
    //     }
    // },
    //提交密码
    toSublimePassword(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alertPassword({
            password: this.ruleForm.oldPass,
            new_password: this.ruleForm.pass
          }).then(res => {
            // console.log(res);
            if (res.data.msgNo == 1) {
              // localStorage.clear();
              removeStore("htcx_webtoken"); //存储本次token
              removeStore("htcx_autologin"); //下次是否自动登录
              removeStore("htcx_uid"); //uid
              removeStore("htcx_userInfo"); //uid
              sessionStorage.clear();
              api.resetServer();
              dialogConfirm('success', res.data.message);
              this.oldePassError = false;
              this.$router.push('/login')
            } else {
              this.oldePassError = true;
              this.errPassword = JSON.parse(JSON.stringify(this.ruleForm.oldPass));
              this.$refs[formName].validate((valid) => {});
            }
          })
        } else {
          return false;
        }
      });
    },
  }
};

</script>
<style lang="scss" scoped>
@import "../../style/mixin.scss";
.content {
  width: 100%;
  min-height: 100%;
  background-color: white;
  padding: 40px;
}

.avatar-uploader {
  width: 90px;
  height: 90px;
  line-height: 90px;
  position: relative;
  top: 55px;
}

.avatar-uploader .el-upload {
  width: 90px;
  height: 90px;
  line-height: 90px;
  cursor: pointer;
  overflow: hidden;
}

.avatar {
  width: 90px !important;
  height: 90px;
  border-radius: 50%;
  position: relative;
}

.picture-btn {
  position: relative;
  left: -80px;
  top: 25px;
  color: #4A90E2;
}

.picture-btn:hover {
  text-decoration: underline;
}

.cursor {
  cursor: pointer;
}

.personal-nav-item {
  display: inline-block;
  height: 34px;
  color: #35383A;
  margin-right: 44px;
}

.personalActive {
  color: #4A90E2;
  border-bottom: 1px solid #4A90E2
}

.personal-info,
.personal-password {
  padding-left: 102px;
  padding-top: 40px;
  font-size: 14px;
}

.personal-info-icon {
  margin-bottom: 20px;
}

.personal-info-icon>div:nth-child(1) {
  position: relative;
  top: -40px;
}

.editor-box {
  vertical-align: top;
  cursor: pointer;
}

.icon-editor {
  display: inline-block;
  width: 20px;
  height: 20px;
  position: relative;
  top: 5px;
  background: url('../../images/personal-editor1.png') no-repeat center;
  background-size: 100%;
}

.editor-btn {
  color: #35383A;
  font-size: 12px;
}

.editor-box:hover .editor-btn {
  color: #4A90E2;
}

.editor-box:hover .icon-editor {
  background: url('../../images/personal-editor.png') no-repeat center;
  background-size: 100%;
}

.personal-info-line .isEditorBtn {
  display: none
} // .isEditorBtn
.personal-info-line div {
  display: inline-block;
  line-height: 60px;
}

.personal-info-line>div:nth-child(1) {
  width: 100px;
  text-align: right;
  color: #A7A9AF;
}

.personal-info-line>div:nth-child(2) {
  width: 304px;
  margin-left: 46px;
  color: #35383A
}

.personal-editor-sublim {
  padding: 50px 0 0 266px;
}

.personal-editor-sublim button {
  width: 116px;
}

.personal-cancle-btn {
  border: 1px solid #4A90E2;
}

.personal-cancle-btn span {
  color: #4A90E2;
}

.personal-success-btn span {
  color: white;
}

.ruleForm {
  width: 430px;
}

</style>



